<template>
  <div class="test">
    <v-container>
      <v-row>
        <v-col class="col-4 col-xs-6 offset-sm-4 text-center">
          <v-btn ref="play" v-if="stimuli.correct" size="x-large" @click="play"
            ><v-icon> mdi-volume-high </v-icon></v-btn
          >
        </v-col>
        <v-col class="col-4 col-xs-6 text-center text-sm-right"
          ><h2 class="test_title">Stimulus {{ index + 1 }}</h2></v-col
        >
      </v-row>
      <v-row class="test_clocks pt-8 pb-8">
        <v-col
          v-for="(stimulus, i) in stimuli.vals"
          :key="i"
          class="pa-8 pt-sm-16 pb-sm-16 v-col-12 v-col-sm-4"
        >
          <v-btn
            class="test_clock"
            :ref="'clock' + i"
            :style="{ transform: 'rotateY(' + index * 360 + 'deg)' }"
          >
            <app-clock
              v-model:time="stimulus.val"
              @click="answer(stimulus.val, i)"
            />
          </v-btn>
        </v-col>
        <div
          class="test_clocks_overlay"
          :class="{ 'test_clocks_overlay-done': done }"
        >
          <div class="test_clocks_text">完了</div>
        </div>
      </v-row>
      <v-row class="text-center">
        <v-col cols="12">
          <transition name="fade">
            <h1
              v-if="correct !== null"
              :class="[correct ? 'text-success' : 'text-error']"
            >
              {{ correct ? "正解" : "誤答" }}
            </h1>
          </transition>
        </v-col>
      </v-row>
    </v-container>
    <audio controls class="test_mp3" ref="mp3">
      <source
        v-if="stimuli.correct"
        :src="require(`@/assets/audio/${fileName}.mp3`)"
        type="audio/mpeg"
      />
      Your browser does not support the audio element.
    </audio>
    <audio class="test_mp3" ref="correct">
      <source :src="require(`@/assets/audio/correct.mp3`)" type="audio/mpeg" />
      Your browser does not support the audio element.
    </audio>
    <audio class="test_mp3" ref="wrong">
      <source :src="require(`@/assets/audio/wrong.mp3`)" type="audio/mpeg" />
      Your browser does not support the audio element.
    </audio>
  </div>
</template>

<script>
import StimuliService from '@/services/stimuli.service'

export default {
  name: 'Test',
  data: () => ({
    stimuliService: new StimuliService(),
    stimuli: {},
    index: 0,
    last: 3,
    correct: null,
    done: false
  }),
  computed: {
    fileName () {
      let fileName
      if (this.stimuli.correct) {
        fileName = this.stimuli.correct[0] + '時'
        if (this.stimuli.correct[1]) {
          fileName += this.stimuli.correct[1] + '分'
        }
      }
      return fileName
    }
  },
  mounted () {
    this.getStimuli()
  },
  methods: {
    getStimuli () {
      this.stimuli = this.stimuliService.getStimuli(this.index)
      this.$refs.mp3.load()
      this.play()
    },
    play () {
      this.$refs.mp3.volume = 1
      this.$refs.mp3.currentTime = 0
      this.$refs.mp3.play()
      this.correct = null
    },
    answer (answer, i) {
      this.correct =
        JSON.stringify(answer) === JSON.stringify(this.stimuli.correct)
      if (this.correct) {
        this.$refs.correct.volume = 1
        this.$refs.correct.currentTime = 0
        this.$refs.correct.play()
      } else {
        this.$refs.wrong.volume = 1
        this.$refs.wrong.currentTime = 0
        this.$refs.wrong.play()
      }
      this.$refs['clock' + i].$el.blur()
      setTimeout(() => {
        this.next()
      }, 2000)
    },
    next () {
      if (this.index < this.last) {
        this.index++
        this.getStimuli()
      } else {
        this.done = true
      }
    }
  }
}
</script>

<style lang="scss">
.test {
  &_title {
    line-height: 52px;
  }
  &_clocks {
    position: relative;
    &_overlay {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: #000;
      opacity: 0;
      border-radius: 8px;
      z-index: 1;
      transition: opacity 500ms;
      pointer-events: none;
      display: flex;
      justify-content: center;
      align-items: center;
      &-done {
        pointer-events: all;
        opacity: 0.2;
      }
    }
    &_text {
      color: #fff;
      font-size: 128px;
    }
  }
  &_clock {
    &.v-btn.v-btn--density-default {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 27vw;
      height: 27vw;
      max-width: 202px;
      max-height: 202px;
      min-width: 160px;
      min-height: 160px;
      padding: 0;
      border-radius: 50%;
      transition: transform 500ms;
    }
    &-disabled {
      pointer-events: none;
    }
  }
  &_mp3 {
    display: none;
  }
}
</style>
